<template>
    <div class="loan">
      <ul class="loan_list">
        <li>
          <span class="loan_list_title" ></span>
          <input type="text" placeholder="3000" class="loan_list_input" v-model="amount" v-on:change="check_money">
        </li>
        <li @click="get_bank(2)">
          <span class="loan_list_title"></span>
          <p class="loan_list_select">{{ purpose }}</p>
          <i class="icon_arrow"></i>
        </li>
        <li @click="get_bank(3)">
          <span class="loan_list_title"></span>
          <p class="loan_list_select">{{ time_limit }}</p>
          <i class="icon_arrow"></i>
        </li>
        <li>
          <span class="loan_list_title"></span>
          <p class="loan_list_select">{{ repayment_day }}<span>()</span></p>
          <i class="icon_todo" @click="open_todo"></i>
        </li>
        <li @click="get_bank(4)">
          <span class="loan_list_title"></span>
          <p class="loan_list_select">{{ source }}</p>
          <i class="icon_arrow"></i>
        </li>
      </ul>
      <p class="receipt_account" @click="get_bank(6)">
        <span class="loan_list_title"></span>
        <i v-bind:class="[icon_bank]"></i>
        <span>{{ bank_name }}</span>
        <i class="icon_arrow"></i>
      </p>
      <p class="risk_tip"></p>
      <p class="next_step"></p>
      <div class="shadow" v-if="shadow_flag"></div>
      <div class="popup" v-if="popup_flag">
        <p class="popup_title">{{popup_title}}</p>
        <p class="popup_text" v-if="popup_text_flag">{{ popup_text }}</p>
        <p class="popup_btn" @click="close_popup"></p>
      </div>
      <div class="selects" v-if="selects_flag">
        <p class="selects_title">{{ selects_title }}</p>
        <ul class="selects_list">
          <li v-for="(data,index) in select_list_data" @click="get_val(index)">
            {{ data.data_name }}
          </li>
        </ul>
        <p class="selsects_cancel" @click="close_selects"></p>
      </div>
      <div class="selects" v-if="selects2_flag">
        <p class="selects_title">{{ selects_title }} <span @click="goto_allbank"></span></p>
        <ul class="selects_list selects2_list">
          <li>
            <img src="../assets/images/logo_js.png" alt="" class="icon_bank">
            <span> 9215</span>
          </li>
        </ul>
        <p class="selsects_cancel" @click="close_selects"></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "loan",
      data () {
        return {
          amount:'',//
          purpose:'',//
          time_limit:'',//
          repayment_day:'12',//
          source:'',//
          icon_bank:'logo_ghh',//
          bank_name:'9215',//
          popup_title:'3000',//
          popup_text:'1129-3128 716816 ',//
          shadow_flag:false,//
          popup_flag:false,//
          popup_text_flag:false,//
          selects_flag:false,//
          selects2_flag:false,//
          selects_title:'',//
          select_list_data:[],//
          numbers:0

        }
      },
      methods:{
        close_popup:function(){
          this.shadow_flag = false;
          this.popup_flag = false;
        },
        open_todo:function(){
          this.shadow_flag = true;
          this.popup_flag = true;
          this.popup_text_flag = true;
          this.popup_title = '';
        },
        check_money(){
          if(this.amount < 3000){
            this.shadow_flag = true;
            this.popup_flag = true;
            this.popup_title = '3000';
            this.amount = '';
          }
        },
        get_bank(t){
          var that = this;
          that.shadow_flag = true;
          that.numbers = t;
          switch (t){
            case 2:
              that.selects_flag = true;
              that.selects_title = '';
              that.select_list_data = that.global.usage_loan;
              break;
            case 3:
              that.selects_flag = true;
              that.selects_title = '';
              that.select_list_data = that.global.term_loan;
              break;
            case 4:
              that.selects_flag = true;
              that.selects_title = '';
              that.select_list_data = that.global.source_loan;
              break;
            case 6:
              this.$ajax.post(that.global.sUrl+'17/getUserBankList',{
                params:{
                  token:'2tgug23veghvegh2veghu2vghev'
                }
              }).then(function (response) {
                that.selects2_flag = true;
                that.selects_title = '';
                console.log(response);
              }).catch(function (error) {
                console.log(error);
              });
              break;
          }
        },
        goto_allbank(){
          this.$router.push('/allow_bank')
        },
        close_selects(){
          this.selects_flag = false;
          this.selects2_flag = false;
          this.shadow_flag = false;
        },
        get_val(i){
          var t = this.numbers;
          switch (t){
            case 2:
              this.purpose = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            case 3:
              this.time_limit = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            case 4:
              this.source = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            // case 6:
            //   this.purpose = this.select_list_data[i].data_name;
            //   break;

          }
        }
      }
    }
</script>

<style scoped>
  .loan{
    width:100%;
    height:100%;
    background: #F2F2F2;
  }
  .loan_list{
    width:100%;
    background: #fff;
  }
  .loan_list li{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    border-top:1px solid #F0F0F0;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
  }
  .loan_list_title{
    display: inline-block;
    width:26%;
  }
  .loan_list_input{
    font-size:15px;
    width:180px;
  }
  .loan_list_select{
    display: inline-block;
    width:180px;
  }
  .loan_list_select span{
    color:#999;
    font-size:13px;
    margin-left:8px;
  }
  .icon_arrow{
    display: block;
    width:16px;
    height:16px;
    background: url("../assets/images/icon_next.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-8px;
  }
  .icon_todo{
    display: block;
    width:14px;
    height:14px;
    background: url("../assets/images/icon_todo.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-7px;
  }
  .receipt_account{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
    margin-top:10px;
    background: #fff;
  }
  .receipt_account span{
    display: inline-block;
    vertical-align: middle;
  }
  .logo_ghh{
    display: inline-block;
    width:20px;
    height:20px;
    background: url("../assets/images/logo_ghh.png") no-repeat;
    background-size:100%;
    vertical-align: middle;
    margin-right:5px;
  }
  .risk_tip{
    padding:0 15px;
    box-sizing: border-box;
    line-height: 18px;
    color:#999;
    font-size:13px;
    margin-top:32px;
  }
  .next_step{
    width:95%;
    margin:36px auto;
    text-align: center;
    font-size:15px;
    line-height: 45px;
    color:#fff;
    background: #E3E3E3;
    border-radius: 2px;
  }
  .popup{
    width:270px;
    border-radius: 7px;
    overflow: hidden;
    position: fixed;
    top:20%;
    left:50%;
    margin-left:-135px;
  }
  .popup_title{
    width:100%;
    box-sizing: border-box;
    padding:30px 0;
    text-align: center;
    background: #fff;
    font-size:18px;
    color:#333;
  }
  .popup_text{
    width:100%;
    box-sizing: border-box;
    padding:0 20px 20px;
    font-size:14px;
    color:#999;
    line-height: 20px;
    background: #fff;
    margin-top:-15px;

  }
  .popup_btn{
    color:#108EE9;
    font-size:18px;
    text-align: center;
    padding:16px 0;
    background: #fff;
    border-top:1px solid #ddd;
  }
  .selects{
    width:100%;
    background: #F2F2F2;
    text-align: center;
    color:#333;
    line-height: 50px;
    position: fixed;
    left:0;
    bottom:0;
  }
  .selects .selects_title{
    color:#999;
    background: #fff;
    position:relative;
  }
  .selects_list{
    background: #fff;
    margin-bottom:10px;
  }
  .selects_list li{
    border-top:1px solid #F0F0F0;
  }
  .selsects_cancel{
    background: #fff;
  }
  .selects_title span{
    color:#4A90E2;
    position:absolute;
    right:15px;
    top:0;
  }
  .icon_bank{
    display: inline-block;
    width:20px;
    height:20px;
    vertical-align: middle;

  }
  .selects2_list li{
    text-align: left;
    padding-left:15px;
    box-sizing: border-box;
  }
  .selects2_list li>span{
    display: inline-block;
    vertical-align: middle;

  }
</style>



